@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  
}

a {
  color: inherit;
  text-decoration: none;
}

.sec {
  width: 100%;
  padding: 70px 0 70px 0;
  scroll-margin-top: 50px;
}

.flexbox {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.sec-title {
  background: rgba(0, 0, 0, 0);
  font-weight: 100;
  font-size: 1.3rem;
  text-align: center;
  margin-bottom: 70px;
  letter-spacing: 0.2em;
}

@media screen and (max-width: 768px) {
  .sec {
    padding: 40px 0 40px 0;
  }

  .sec-title {
    margin-bottom: 40px;
  }
}
.Typist .Cursor {
    display: inline-block; }
    .Typist .Cursor--blinking {
      opacity: 1;
      -webkit-animation: blink 1s linear infinite;
              animation: blink 1s linear infinite; }
  
@-webkit-keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } }
  
@keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } }
.header {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  color: #3a8590;
  background: #dfdcd4;
  transition: all 0.2s ease-in-out;
}

.header2 {
  width: 100%;
  position: fixed;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
}

.container {
  max-width: 75rem;
  width: 100%;
  padding: 0 2rem 0 2rem;
}

.head {
  display: flex;
  justify-content: space-between;
}

.logo {
  padding: 0 0.3rem 0 0.3rem;
  margin-right: 6rem;
  font-size: 1.2rem;
  font-weight: 700;
  transition: all 0.2s ease-in-out;
}

.logo:hover {
  cursor: pointer;
}

.header .logo:hover {
  background-color: #3a8590;
  color: #dfdcd4;
}

.menu {
  text-align: center;
}

.menu ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.menu li {
  padding: 0 1rem 0 1rem;
  letter-spacing: 0.1em;
  font-weight: 300;
  transition: all 0.2s ease-in-out;
}

@media screen and (max-width: 560px) {
  .container {
    padding: 0;
  }
  
  .logo {
    display: none;
  }

  .menu {
    width: 100%;
  }

  .menu ul {
    display: flex;
    justify-content: center;
    padding: 0;
  }

  .menu li {
    padding: 0 0.3rem 0 0.3rem;
    letter-spacing: 0;
    font-size: 0.9rem;
  }
}

.main {
  min-height: 540px;
  text-align: center;
  padding: 50px 0 50px 0;
  background-color: #dfdcd4;
  color: #3a8590;
  font-family: "Lato", sans-serif;
  font-weight: 400;
}

.flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.typing {
  font-size: 2rem;
  margin: 1rem;
}

.typing span {
  font-size: 3rem;
  padding: 1.2rem 0 1rem 0;
  font-weight: 700;
}

.about {
  background: #fff;
  color: #000;
}

.maxw {
  max-width: 75rem;
  width: 100%;
}

.flex2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.pic {
  margin: 1rem;
}

.pic img {
  width: auto;
  max-width: 100%;
  transition: all 0.3s ease-in-out;
  border-radius: 6px;
}

.intro {
  margin: 1rem;
  max-width: calc(75rem - 600px);
  flex: 1 1;
  min-width: 55%;
}

.iAlign {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
}

.intro h2 {
  font-size: 1.7rem;
  padding-top: 2rem;
  text-align: center;
}

.intro p {
  font-size: 1.2rem;
}

.qLine {
  width: 100%;
}

.lAlign {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.quotes {
  color: #989898;
  font-size: 2rem;
  width: 2rem;
  padding: 1rem;
  transition: all 0.15s ease-in-out;
}

.qCon {
  width: 100%;
}

.qLine,
.word {
  margin: 2rem 0 2rem 0;
}

@media screen and (max-width: 720px) {
  .qLine {
    text-overflow: hidden;
  }

  .intro h2 {
    font-size: 1.6rem;
  }

  .quotes {
    font-size: 1.5rem;
  }
}

.skill {
  background-color: #3a8590;
  color: #fff;
}

.maxw .sUl {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.liItem {
  font-size: 5rem;
  margin: 2rem;
  width: 7rem;
}

.liItem p {
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  color: #fff;
}

.hov {
  background: #fff;
  color: #3a8590;
  padding: 1rem;
  text-align: center;
  border-radius: 0.5rem;
}

.sIcons {
  transition: all 0.2s ease-in-out;
}

.hov p {
  transition: all 0.2s ease-in-out;
}

.hov:hover .sIcons {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.hov:hover p {
  color: #3a8590;
}

.sName {
  font-size: 1rem;
}

.project {
	background: #b5d2ce;
	color: #000;
}

.cardFlex {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.cardWrapper {
	box-sizing: border-box;
	background: rgba(255, 255, 255, 0.8);
	width: 100%;
	padding: 2rem;
	margin-bottom: 4rem;
}

.cardTitle {
	text-align: center;
	margin-bottom: 0.5rem;
}

.projectDate {
	text-align: center;
	margin-bottom: 1.5rem;
}

.cardContent {
	display: flex;
}

.projectPic img {
	max-width: 360px;
	border-radius: 10px;
	box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}

.cardDesc {
	padding-left: 2rem;
}

.techs {
	margin: 1rem 0;
}

.tech {
	border: 2px solid #000;
	border-radius: 1rem;
	padding: 0 0.3rem 0 0.3rem;
	margin-right: 0.3rem;
}

.github {
	font-size: 2rem;
}

.demo {
	font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
	.cardContent {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.projectPic img {
		margin-bottom: 1rem;
		width: 240px;
	}

	.cardDesc {
		padding: 0;
	}
}

.contact {
  background: #fff;
  color: #000;
}

.cIcons {
  display: flex;
  flex-direction: column;
}

.iFlex:hover {
  color: #3a8590;
  border-color: #3a8590;
}

.ci {
  font-size: 3rem;
  position: relative;
  left: -3px;
}

.addr {
  font-size: 1.2rem;
  max-height: 4rem;
  padding: 0 1rem 0 1rem;
}

.iFlex {
  border: 3px solid black;
  max-height: 48px;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  margin: 1.5rem;
  transition: all 0.3s ease;
}

@media screen and (max-width: 720px) {
  .addr {
    display: none;
  }

  .ci {
    left: 0px;
    font-size: 6rem;
  }

  .iFlex {
    border: none;
    max-height: 100%;
  }
}

footer {
  background-color: #dfdcd4;
  color: #3a8590;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer p {
  font-size: 0.9rem;
}

